.login{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25rem;
    height: 18rem;
    border: 1px solid rgb(210, 210, 210);
    border-radius: 6px;
    background-color: rgba(0, 0, 0, 0);
    box-shadow: 0px 1px 10px -4px #5e5e5e;
}

.vertical{
    width: 0.5px;
    height: 85%;
    background-color: rgb(210, 210, 210);
}

.l,
.r{
    flex: 1 1 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height:100%;
    transition: all ease-in 300ms;
}

.r{
    justify-content: space-around;
}

.l:hover,
.r:hover{
    flex: 1 1 62%;
}

@media screen and (max-width: 48rem) {
    .login{
        width: 90%;
        height: 90%;
        padding: 1rem;
        flex-direction: column-reverse;
    }

    .vertical{
        padding: 1px 0;
        width: 85%;
        height: 0.5px;
        background-color: rgb(210, 210, 210);
    }

    .l,
    .r{
        flex: 1 1 50%;
    }
    
    .l:hover,
    .r:hover{
        flex: 1 1 50%;
    }
}