.main{
    width: 100%;
    height: 100vh;
    background-color: white;
}
.main.dark{
    background-color: rgb(30, 30, 30);
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3rem;
    padding: 0.5rem 1rem;
    background-color: rgb(50, 50, 50);
}

.btns{
    display: flex;
    justify-content: space-between;
    width: 3rem;
}

.userSection,
.previewSection{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgb(200, 200, 200);
    width: 100%;
    padding: 3rem 0;
}

.userSection.dark,
.previewSection.dark{
    background-color: rgb(30, 30, 30);
    border-bottom: 1px solid rgb(100, 100, 100);
}

.previewSection2{
    /* margin:auto; */
    width: 80%;
    max-width: 56rem;
}

.description{
    margin-bottom: 0.5rem;
    color: rgb(50, 50, 50);
}
.description.dark{
    color: rgb(230, 230, 230);
}

.footer{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 70px;
    color: rgb(50, 50, 50);
}
.footer.dark{
    background-color: rgb(30, 30, 30);
    color: rgb(230, 230, 230);
}

.link,
.contact{
    color:rgb(81, 185, 250);
    text-decoration:none;
    font-weight: 300;
    font-size: 0.8rem;
    margin-right: 2rem;
}

.contact{
    margin-right: 0;
}

@media screen and (max-width: 60rem) {
    .titleBox{
        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
    }
}